* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 1rem;
  font-weight: 400;
  color: #212529;
  text-align: center;
  background: #b9f1d6;
  margin: 10px 0;
}

h1 {
  color: black;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
  box-shadow: inset 3px 3px 3px rgba(0,0,0,0.4);
  padding: 10px;
  border: 1px solid grey;
  border-radius: 5px;
  width: 40%;
  margin: 0px auto 15px auto;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  letter-spacing: 2px;
  font-size: 47px
}

h2 {
  width: 40%;
  background-color: #69d2cd;
  padding: 7px;
  outline: solid 1px gray;
  border-radius: 5px 5px 3px 3px;
  font-family: Russo One;
  font-size: 1.5rem;
  color: black;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.5);
}

#editor, h2 {
  margin: 0px auto 0px auto;
}

h3 {
  width: 60%;
  background-color: #69d2cd;
  padding: 7px;
  outline: solid 1px gray;
  border-radius: 5px 5px 3px 3px;
  font-family: Russo One;
  font-size: 1.5rem;
  color: black;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.5);
}

#editor, h3 {
  margin: 0px auto 0px auto;
}

#editor {
  border: solid 1px;
  margin-bottom: 15px;
  background-color: #f1f6ce;
  border-radius: 0px 0px 5px 5px;
  width: 40%;
  height: 100%;
  min-height: 300px;
  resize: vertical;
  outline: none;
  font-size: 0.875rem;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.5);
}

#preview {
  border: solid 1px;
  background-color: #f1f6ce;
  border-radius: 0px 0px 5px 5px;
  width: 60%;
  height: 100%;
  min-height: 300px;
  resize: vertical;
  outline: none;
  font-size: 0.875rem;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.5);
}

#pencil-edit {
  margin-right: 8px;
  width: 20px;
  height: 20px;
}

#clipboard {
  margin-right: 8px;
  width: 20px;
  height: 20px;
}

#toggle-1 {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: none;
  background-color: #b9f1d6;
}

#toggle-1:hover {
  background-color: yellow;
}

#toggle-1 svg {
  width: 16px;
  height: 16px;
  fill: black solid 4px;
}

#toggle-1:hover svg {
  fill: black 2px;
}



